* {
    box-sizing: border-box;
}

/* Light mode */
body {
    font-family: 'Geologica', sans-serif;
}

@media (prefers-color-scheme: light) {
    body {
        background-color: white;
        color: black;
        background-image: url(data:image/png;base64,);
    }

    #roman, #hangul {
        background-color: #dfe0e2;
        color: #a5a6a9;
        -webkit-border-radius: 12px;
        -moz-border-radius: 12px;
        border-radius: 12px;
    }

    a {
        color: #000000;
    }

    #help {
        color: #000000;
    }
}

/* Dark mode */
@media (prefers-color-scheme: dark) {
    body {
        background-color: #0d0f05;
        color: #fff;
        background-image: url(data:image/png;base64,);
    }

    #roman, #hangul {
        background-color: #333333;
        color: #fff;
        -webkit-border-radius: 12px;
        -moz-border-radius: 12px;
        border-radius: 12px;
    }

    a {
        color: #9e9e9e;
    }

    #help {
        fill: #fff;
    }
}

#title {
    padding: 20px;
    padding-bottom: 0px;
}

#romanDiv {
    width: 50%;
    height: 80%;
    float: left;
    padding: 20px;
    padding-bottom:40px;
}

#hangulDiv {
    width: 50%;
    height: 80%;
    float: right;
    padding: 20px;
    padding-bottom:40px;
}

textarea {
    width: 100%;
    height: 100%;
    padding: 20px;
}

#about {
    position: fixed;
    left: 0;
    bottom: 10;
    width: 100%;
    color: white;
    text-align: center;
}

.tooltip {
    position:relative;
}

.tooltip:before {
    content: attr(data-text);
    position:absolute;
    top:50%;
    transform:translateY(-50%);
    left:100%;
    margin-left:15px;
    width:250px;
    padding:10px;
    border-radius:10px;
    background:#000;
    color: #fff;
    text-align:center;
    display:none;
    opacity:0;
    transition:.3s opacity;
}

.tooltip:hover:before {
    display:block;
    opacity:1;
}

.tooltip:after {
    content: "";
    position:absolute;
    left:100%;
    margin-left:-5px;
    top:50%;
    transform:translateY(-50%);
    border:10px solid #000;
    border-color: transparent black transparent transparent;
    display:none;
    opacity:0;
    transition:.3s;
}

.tooltip:hover:before, .tooltip:hover:after {
    display:block;
    opacity:1;
}

@media screen and (max-width: 1000px) {
    #romanDiv, #hangulDiv {
        width: 100%;
        height: 40%;
        padding: 20px;
    }

    #romanDiv {
        padding-bottom:30px;
    }

    #hangulDiv {
        padding-bottom:0px;
    }
}
